
.card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  border-radius: 5px;
  transition: 0.3s;
  // height: ;
}

.card-container {
  background:$background-color;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(5,1fr);
  padding: 16px 16px;
}

.card-image-container{
  display: block;
  width:100%;
  border-radius: 5px 5px 0 0;
}
.netflixLogo{
    position:absolute;
    margin: 15px 15px;
    height:45px;
    width:30px;
  }
.card:hover {
  position: relative;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.card-body{
  width: auto;
  color: $font-color;
  font-family: $font-family;
  background-color: rgba(20, 20, 20,0.2);
  border: 1px solid rgb(145, 140, 140);
  border-radius: 5px;
}
.card-title{
  height: 40px;
  padding:0 15px;
  font-size: medium;
}
.detailsButton{
  padding: 15px 40px;
  justify-content: center;
  background-color:#fff;
  height:100%;
  width:auto;
  display: block;
  margin:10px auto;
  border:none; 
  color: $font-color;
  font-size: $font-size;
  font-family: $font-family;
}
.detailsLink{
  text-decoration: none;
  color: black;
  font-family: $font-family;
}
.detailsButton:hover{
  transition: 0.70s;
  background-color: rgb(70, 69, 67);
  .detailsLink{
    transition: 0.30s;
    color: #fff;
  }
}